import React, { Component } from "react";
import { Link } from "react-router-dom";
import { app, base, facebookProvider } from '../base';

class Register extends Component {
      constructor(props) {
        super(props)
        this.authWithEmailPassword = this.authWithEmailPassword.bind(this)
        this.state = {
          redirect: false
        }
      }

    authWithEmailPassword(event) {
    event.preventDefault()

    const email = this.emailInput.value
    const password = this.passwordInput.value

    app.auth().fetchProvidersForEmail(email)
      .then((providers) => {
        if (providers.length === 0) {
          // create user
          return app.auth().createUserWithEmailAndPassword(email, password)
        } else if (providers.indexOf("password") === -1) {
          // they used facebook
          this.loginForm.reset()
          console.log("Try alternative login.");
        } else {
          // sign user in
          return app.auth().signInWithEmailAndPassword(email, password)
        }
      })
      .then((user) => {
        if (user && user.email) {
          this.loginForm.reset()
          this.props.setCurrentUser(user)
          this.setState({redirect: true})
        }
      })
      .catch((error) => {
        console.log('base', base.config.apiKey);
        console.log('error occured', error);
      })
  }




render() {
return    (
        <div className="container">
       	<form  onSubmit={(event) => { this.authWithEmailPassword(event) }} ref={(form) => { this.loginForm = form }}
        className="form-signin col-lg-4 justify-content-center">
              <h2 className="form-signin-heading">Create an account</h2>
              <label for="inputEmail" ref={(input) => { this.emailInput = input }} className="sr-only">Email address</label>
              <input type="email" id="inputEmail" className="form-control" placeholder="Email address" required="" autofocus=""/>
              <label for="inputPassword" ref={(input) => { this.passwordInput = input }} className="sr-only">Password</label>
              <input type="password" id="inputPassword" className="form-control" placeholder="Password" required=""/>

              <button className="btn btn-lg btn-primary btn-block" type="submit">Registe</button>
            </form>
         
        </div>
        );
};
};
export default Register;



