import React, { Component } from "react";
import { Link } from "react-router-dom";

import Rebase from "re-base";

class Header extends Component {
  constructor(props) {
    super(props)
  }

 
render() {

    return(

          <nav className="navbar navbar-fixed-top navbar-toggleable-md navbar-light bg-faded">
                <button className="navbar-toggler navbar-toggler-right" type="button" data-toggle="collapse" data-target="#exCollapsingNavbar2">
                    ☰
                </button>
                <a className="navbar-brand" href="#">☺</a>
                <div className="collapse navbar-collapse" id="exCollapsingNavbar2">
                    <ul className="navbar-nav ml-auto">
                        <li className="nav-item">
        					   <Link className="nav-link" to="/page2">Go to German</Link>
        				</li>
                        <li className="nav-item">
                               <Link className="nav-link" to="/page2">Go to English</Link>
                        </li>
                        {this.props.authenticated ? (<p> hi </p>)
                        :
                        <ul className="navbar-nav">
                        <li className="nav-item">
                               <Link className="nav-link" to="/login"><i className="fa fa-user" aria-hidden="true"></i> Login</Link>
                        </li>
                        <li className="nav-item">
                               <Link className="nav-link" to="/register"><i className="fa fa-sign-in" aria-hidden="true"></i> Register</Link>
                        </li>
                        </ul>
                    }
                    </ul>
                </div>
            </nav>
    ) };
};

export default Header;



